<template>
  <div id="translateForm">
    <div class="col-md-offset-3">
      <form id="transForm" v-on:submit="formSubmit" class="form-inline">
        <input
          type="text"
          class="form-control"
          style="width:200px"
          v-model="textToTranslate"
          placeholder="输入需要翻译的内容"
        >
        
        <select v-model="language" class="form-control" data-toggle="dropdown">
          <option value="en">English</option>
          <option value="ru">Russian</option>
          <option value="ko">Korean</option>
          <option value="ja">Janpenese</option>
        </select>
        <input class="btn btn-primary" type="submit" value="翻译">
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "translateForm",
  data: function() {
    return {
      textToTranslate: "",
      language: ""
    };
  },
  methods: {
    formSubmit: function(e) {
      // alert(this.textToTranslate);
      this.$emit("formSubmit", this.textToTranslate, this.language);
      e.preventDefault();
    }
  }
};
</script>

<style>
.col-md-offset-3 {
  width: 400px;
  margin: 0 auto;
}
#transForm {
}
</style>



