<template>
  <div id="translateOutput">
    <h1>{{ translatedText }}</h1>
  </div>
</template>

<script>
export default {
  name: "translateOutput",
  props: ["translatedText"]
};
</script>

<style scoped>
#translateOutput {
  margin: 30px auto;
  font-size: 30px;
}
</style>




